import React, { useMemo, useCallback } from 'react';
import { Box, FormControl, InputLabel, MenuItem, Select, useTheme } from '@mui/material';
import { ResponsiveContainer, LineChart, XAxis, YAxis, Tooltip, Line } from 'recharts';
import { WeatherData } from '../types/weather';

interface ChartDataItem extends WeatherData {
    wind_speed: number;
    [key: `temp_profile_${number}`]: number;
    [key: `cloud_profile_${number}`]: number;
    [key: `rh_profile_${number}`]: number;
    [key: `wind_profile_${number}`]: number;
    [key: `wind_profile_high_res_${number}`]: number;
}

interface WeatherTimePlotProps {
    data: WeatherData[];
    temperatureUnit: 'C' | 'F';
    fetchTimestamp: number;
}

const WeatherTimePlot: React.FC<WeatherTimePlotProps> = ({ data, temperatureUnit, fetchTimestamp }) => {
    const theme = useTheme();
    const [selectedMetric, setSelectedMetric] = React.useState('temp2m');
    const [isTooltipActive, setIsTooltipActive] = React.useState(false);

    const convertTemperature = useCallback((celsius: number): number => {
        if (temperatureUnit === 'F') {
            return (celsius * 9 / 5) + 32;
        }
        return celsius;
    }, [temperatureUnit]);

    // Add wind speed to the data
    const chartData: ChartDataItem[] = data.map(item => ({
        ...item,
        wind_speed: item.wind10m.speed,
        temp2m: convertTemperature(item.temp2m)
    }));

    // Define categories using useMemo to prevent recalculation
    const categories = useMemo(() => {
        const baseCategories = [
            { value: 'temp2m', label: `Temperature (°${temperatureUnit})` },
            { value: 'prec_amount', label: 'Precipitation (mm)' },
            { value: 'cloudcover', label: 'Cloud Cover (%)' },
            { value: 'rh2m', label: 'Humidity (%)' },
            { value: 'msl_pressure', label: 'Pressure (hPa)' },
            { value: 'seeing', label: 'Astronomical Seeing (/10)' },
            { value: 'transparency', label: 'Atmospheric Transparency (/10)' },
            { value: 'lifted_index', label: 'Lifted Index' },
            { value: 'snow_depth', label: 'Snow Depth (cm)' },
            { value: 'wind_speed', label: 'Wind Speed' },
        ];

        // Add temperature profile data if available
        if (data[0]?.temp_profile) {
            for (let i = 0; i < data[0].temp_profile.length; i++) {
                const altitude = i + 1;
                baseCategories.push({
                    value: `temp_profile_${altitude}`,
                    label: `Temperature at ${altitude}km (°${temperatureUnit})`
                });
            }
        }

        // Add cloud profile data if available
        if (data[0]?.cloud_profile) {
            for (let i = 0; i < data[0].cloud_profile.length; i++) {
                const altitude = i + 1;
                baseCategories.push({
                    value: `cloud_profile_${altitude}`,
                    label: `Cloud Cover at ${altitude}km (%)`
                });
            }
        }

        // Add high-resolution humidity profile data if available
        if (data[0]?.rh_profile_high_res) {
            for (let i = 0; i < data[0].rh_profile_high_res.length; i++) {
                const altitude = i + 1;
                baseCategories.push({
                    value: `rh_profile_${altitude}`,
                    label: `Humidity at ${altitude}km (%)`
                });
            }
        }

        return baseCategories;
    }, [data, temperatureUnit]);

    // Process the data for the chart
    const processedChartData = useMemo(() => {
        const processed = [...chartData];

        // Add temperature profile data
        if (data[0]?.temp_profile) {
            for (let i = 0; i < data[0].temp_profile.length; i++) {
                const altitude = i + 1;
                processed.forEach(item => {
                    if (item.temp_profile) {
                        item[`temp_profile_${altitude}`] = convertTemperature(item.temp_profile[i]);
                    }
                });
            }
        }

        // Add cloud profile data
        if (data[0]?.cloud_profile) {
            for (let i = 0; i < data[0].cloud_profile.length; i++) {
                const altitude = i + 1;
                processed.forEach(item => {
                    if (item.cloud_profile) {
                        item[`cloud_profile_${altitude}`] = item.cloud_profile[i];
                    }
                });
            }
        }

        // Add humidity profile data
        if (data[0]?.rh_profile_high_res) {
            for (let i = 0; i < data[0].rh_profile_high_res.length; i++) {
                const altitude = i + 1;
                processed.forEach(item => {
                    if (item.rh_profile_high_res) {
                        item[`rh_profile_${altitude}`] = item.rh_profile_high_res[i];
                    }
                });
            }
        }

        // Add wind profile data
        if (data[0]?.wind_profile) {
            for (let i = 0; i < data[0].wind_profile.length; i++) {
                const altitude = i + 1;
                processed.forEach(item => {
                    if (item.wind_profile) {
                        item[`wind_profile_${altitude}`] = item.wind_profile[i].speed;
                    }
                });
            }
        }

        // Add high-resolution wind profile data
        if (data[0]?.wind_profile_high_res) {
            for (let i = 0; i < data[0].wind_profile_high_res.length; i++) {
                const altitude = i + 1;
                processed.forEach(item => {
                    if (item.wind_profile_high_res) {
                        item[`wind_profile_high_res_${altitude}`] = item.wind_profile_high_res[i].speed;
                    }
                });
            }
        }

        return processed;
    }, [data, chartData, convertTemperature]);

    // Get wind speed lines for the chart
    const windSpeedLines = useMemo(() => {
        if (selectedMetric !== 'wind_speed') return null;

        const lines = [
            <Line
                key="wind_speed_10m"
                type="monotone"
                dataKey="wind_speed"
                stroke="#ff0000"
                name="Wind Speed at 10m"
            />
        ];

        // Add wind profile lines with different shades of red
        if (data[0]?.wind_profile) {
            for (let i = 0; i < data[0].wind_profile.length; i++) {
                const altitude = i + 1;
                lines.push(
                    <Line
                        key={`wind_profile_${altitude}`}
                        type="monotone"
                        dataKey={`wind_profile_${altitude}`}
                        stroke={`rgb(${255 - i * 20}, ${i * 10}, ${i * 10})`}
                        name={`Wind Speed at ${altitude}km`}
                    />
                );
            }
        }

        // Add high-resolution wind profile lines with different shades of red
        if (data[0]?.wind_profile_high_res) {
            for (let i = 0; i < data[0].wind_profile_high_res.length; i++) {
                const altitude = i + 1;
                lines.push(
                    <Line
                        key={`wind_profile_high_res_${altitude}`}
                        type="monotone"
                        dataKey={`wind_profile_high_res_${altitude}`}
                        stroke={`rgb(${255 - i * 20}, ${i * 10}, ${i * 10})`}
                        name={`High Res Wind Speed at ${altitude}km`}
                    />
                );
            }
        }

        return lines;
    }, [data, selectedMetric]);

    return (
        <Box>
            <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel>Select</InputLabel>
                <Select
                    value={selectedMetric}
                    onChange={(e) => setSelectedMetric(e.target.value)}
                    label="Select"
                >
                    {categories.map((category) => (
                        <MenuItem key={category.value} value={category.value}>
                            {category.label}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
            <ResponsiveContainer width="100%" height={400}>
                <LineChart
                    data={processedChartData}
                    onMouseEnter={() => setIsTooltipActive(true)}
                    onMouseLeave={() => setIsTooltipActive(false)}
                >
                    <XAxis
                        dataKey="timepoint"
                        tickFormatter={(value: number) => new Date(fetchTimestamp + value * 3600000).toLocaleTimeString()}
                    />
                    <YAxis
                        domain={['auto', 'auto']}
                        tickFormatter={(value: number) => {
                            if (selectedMetric === 'msl_pressure') {
                                return value.toFixed(1);
                            }
                            if (selectedMetric.startsWith('temp')) {
                                return value.toFixed(1);
                            }
                            return value.toString();
                        }}
                    />
                    <Tooltip
                        labelFormatter={(value: number) => new Date(fetchTimestamp + value * 3600000).toLocaleString()}
                        active={isTooltipActive}
                        contentStyle={{
                            backgroundColor: theme.palette.mode === 'dark' ? '#1e1e1e' : '#ffffff',
                            border: `1px solid ${theme.palette.mode === 'dark' ? '#333' : '#ccc'}`,
                            borderRadius: '4px',
                            color: theme.palette.text.primary,
                        }}
                        itemStyle={{
                            color: theme.palette.text.primary,
                        }}
                        formatter={(value: number, name: string) => {
                            if (selectedMetric.startsWith('temp')) {
                                return [`${value.toFixed(1)}°${temperatureUnit}`, 'Temperature'];
                            }
                            if (selectedMetric === 'wind_speed') {
                                if (name === 'Wind Speed at 10m') {
                                    return [`${value.toFixed(1)} m/s`, 'Wind Speed at 10m'];
                                }
                                if (name.startsWith('Wind Speed at ')) {
                                    return [`${value.toFixed(1)} m/s`, name];
                                }
                                if (name.startsWith('High Res Wind Speed at ')) {
                                    return [`${value.toFixed(1)} m/s`, name];
                                }
                            }
                            return [value, categories.find(c => c.value === selectedMetric)?.label];
                        }}
                    />
                    {selectedMetric === 'wind_speed' ? (
                        windSpeedLines
                    ) : (
                        <Line
                            type="monotone"
                            dataKey={selectedMetric}
                            stroke={theme.palette.mode === 'dark' ? '#ff4444' : '#ff0000'}
                            name={categories.find(c => c.value === selectedMetric)?.label}
                        />
                    )}
                </LineChart>
            </ResponsiveContainer>
        </Box>
    );
};

export default WeatherTimePlot; 