import { WeatherData, WeatherResponse, Location, NominatimResponse } from '../types/weather';

export const fetchWeatherData = async (location: Location): Promise<WeatherData[]> => {
    // Fetch ASTRO, METEO, and CIVIL data
    const [astroResponse, meteoResponse, civilResponse] = await Promise.all([
        fetch(
            `https://www.7timer.info/bin/api.pl?lon=${location.lng}&lat=${location.lat}&product=astro&output=json`
        ),
        fetch(
            `https://www.7timer.info/bin/api.pl?lon=${location.lng}&lat=${location.lat}&product=meteo&output=json`
        ),
        fetch(
            `https://www.7timer.info/bin/api.pl?lon=${location.lng}&lat=${location.lat}&product=civil&output=json`
        )
    ]);

    if (!astroResponse.ok || !meteoResponse.ok || !civilResponse.ok) {
        throw new Error('Failed to fetch weather data');
    }

    const astroData: WeatherResponse = await astroResponse.json();
    const meteoData: WeatherResponse = await meteoResponse.json();
    const civilData: WeatherResponse = await civilResponse.json();

    // Combine the data from all three responses
    return astroData.dataseries.map((astroItem, index) => {
        const meteoItem = meteoData.dataseries[index];
        const civilItem = civilData.dataseries[index];
        return {
            ...astroItem,
            ...meteoItem,
            ...civilItem,
            rh2m: typeof astroItem.rh2m === 'string' ? parseFloat((astroItem.rh2m as string).replace('%', '')) : astroItem.rh2m
        };
    });
};

export const searchLocation = async (query: string): Promise<NominatimResponse[]> => {
    const response = await fetch(
        `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(
            query
        )}&limit=1`
    );
    return response.json();
}; 